* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App {
    width: 800px;
}

body {
    background: linear-gradient(90deg, #b9deed, #efefef);
}

.post-enter {
    transform: translate(-350px);
}

.post-enter-active {
    transform: translate(0px);
    transition: all 500ms ease-in;
}

.post-exit {
    opacity: 1;
}

.post-exit-active {
    transform: translate(-350px);
    transition: all 500ms ease-in;
}

.page__wrapper {
    margin: 30px 0 30px 0;
    display: flex;
    justify-content: center;
}

.page {
    padding: 10px;
    margin: 0 1px;
    border: 1px solid teal;
    cursor: pointer;
}

.page__current {
    border: 2px solid orange;
    font-weight: bold;
}

.error {
    color: red;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.block {
    max-width: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
